.react-contextmenu {
  min-width: 10em;
  color: #373a3c;
  text-align: left;
  background-color: #fff;
  background-clip: padding-box;
  border: 0.05em solid rgba(0, 0, 0, 0.5);
  border-radius: 0.25rem;
  outline: none;
  opacity: 0;
  pointer-events: none;
  -webkit-transition: opacity 250ms ease !important;
  -o-transition: opacity 250ms ease !important;
  transition: opacity 250ms ease !important;
}

.react-contextmenu.react-contextmenu--visible {
  opacity: 1;
  pointer-events: auto;
  z-index: 3;
}

.react-contextmenu-item {
  padding: 0.2em 0.2em;
  font-weight: 400;
  line-height: 1.5;
  color: #373a3c;
  text-align: inherit;
  white-space: nowrap;
  background: 0 0;
  border: 0;
  cursor: pointer;
}

.react-contextmenu-item.react-contextmenu-item--active,
.react-contextmenu-item.react-contextmenu-item--selected {
  color: #fff;
  background-color: #20a0ff;
  border-color: #20a0ff;
  text-decoration: none;
}

.react-contextmenu-item.react-contextmenu-item--disabled,
.react-contextmenu-item.react-contextmenu-item--disabled:hover {
  color: #878a8c;
  background-color: transparent;
  border-color: rgba(0, 0, 0, 0.15);
}

.react-contextmenu-item--divider {
  margin-bottom: 3px;
  padding: 2px 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.15);
  cursor: inherit;
}
.react-contextmenu-item--divider:hover {
  background-color: transparent;
  border-color: rgba(0, 0, 0, 0.15);
}

.react-contextmenu-item.react-contextmenu-submenu {
  padding: 0;
  outline: none;
}

.react-contextmenu-item.react-contextmenu-submenu > .react-contextmenu-item {
}

.react-contextmenu-item.react-contextmenu-submenu
  > .react-contextmenu-item:after {
  content: "â–¶";
  display: inline-block;
  position: absolute;
  right: 7px;
}

.example-multiple-targets::after {
  content: attr(data-count);
  display: block;
}
.react-contextmenu-wrapper {
  width: inherit;
}

.react-contextmenu-item-filtered {
  font-weight: bold;
}
.zebulon-scrollbar-bar {
  background-color: lightgrey;
  border: solid 0.03em grey;
  border-radius: 0.25rem;
}
.zebulon-scrollbar-thumb {
  background-color: grey;
  border: solid 0.03em grey;
  border-radius: 0.25rem;
}



